// Shuffles an array using Fisher-Yates
let shuffle = (arr) => {
    for (let i = arr.length - 1; i >= 1; i--) {
        let j = Math.floor(Math.random() * i)
        let hold = arr[i]
        arr[i] = arr[j]
        arr[j] = hold
    }
}

let shuffleCorrectAnswers = (questionArr) => {
    console.log(questionArr)
    questionArr.forEach(q => {
        console.log(q)
        let correctValue = q.answers[q.correctAnswer]
        console.log(correctValue)
        shuffle(q.answers)
        q.correctAnswer = q.answers.findIndex(v => v === correctValue)
    })
}

class SpaceExQuiz extends Component {
    constructor(props) {
        super(props);
        this.onNext = this.onNext.bind(this)
        this.onReset = this.onReset.bind(this)
        this.state = {
            index: 0
        }
        //shuffle(questionList)
        shuffleCorrectAnswers(questionList)
    }

    onNext() {
        console.log(this.state.index)
        if (this.state.index < questionList.length) {
            this.setState({
                index: this.state.index + 1
            })
        }
    }

    onReset() {
        questionList.forEach(q => {
            q.answeredIndex = undefined
        })
        shuffle(questionList)
        shuffleCorrectAnswers(questionList)
        this.setState({
            index: 0
        })
    }

    renderScore() {
        let score = questionList.filter(v => v.isCorrect()).length
        let fractionCorrect = score / questionList.length
        let message;
        if (fractionCorrect < 0.3) {
            message = "Better luck next time!"
        }
        else if (fractionCorrect < 0.8) {
            message = "Good job!"
        }
        else {
            message = "Excellent!"
        }
        return (
            <body className={"score-section"}>
                <p>Score: {score}/{questionList.length}. </p>
                <p>{message} </p>
                <button onClick={() => {this.onReset()}}>Try again</button>
            </body>
        )
    }

    render() {
        console.log(questionList)
        return (
            <div className="App">
              <header className="App-header">
                Space Exploration Quiz
              </header>
              <hr></hr>
              {this.state.index < questionList.length 
                ?
                    <body>
                        <QuestionCard 
                            showNext={true}
                            question={questionList[this.state.index]}
                            onNext={() => { this.onNext() }}
                            answeredIndex={questionList[this.state.index].answeredIndex}
                        />
                    </body>
                :
                    this.renderScore()
              }
              Previous Questions:
              <div className={"accordion-section"}>
              {questionList.slice(0, this.state.index).map(
                    (q, i) => 
                    <Accordion>
                      <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id={q.text}
                      >
                        <Typography className={useStyles.heading}>
                            {q.isCorrect() ? "Correct: " + q.text : "Incorrect: " + q.text}
                        </Typography>
                      </AccordionSummary>
                      <AccordionDetails>
                        <QuestionCard 
                            answeredIndex={q.answeredIndex}
                            showNext={false}
                            question={q}
                        />
                      </AccordionDetails>
                    </Accordion>
                )}
              </div>
            </div>
        );
    }
}

export default SpaceExQuiz;
